body {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  background-color: hsl(226, 43%, 10%);
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  margin-left: auto;
  margin-right: auto;
  min-height: 100vh;
  width: 91.666667%;
  max-width: 1024px;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bright-blue-bg {
  background-color: hsl(246, 80%, 60%);
}

.dark-blue-bg {
  background-color: hsl(235, 46%, 20%);
}

.desaturated-blue {
  color: hsl(235, 45%, 61%);
}

.pale-blue {
  color: hsl(236, 100%, 87%)
}

.light-orange-bg {
  background-color: hsl(15, 100%, 70%);
}

.soft-blue-bg {
  background-color: hsl(195, 74%, 62%);
}

.light-red-bg {
  background-color: hsl(348, 100%, 68%);
}

.lime-green-bg {
  background-color: hsl(145, 58%, 55%);
}

.violet-bg {
  background-color: hsl(264, 64%, 52%);
}

.yellow-bg {
  background-color: hsl(43, 84%, 65%);
}

.white-border {
  border-color: white;
}

.ModalContainer {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 1.25rem;
}

@media (min-width: 1024px) {
  .ModalContainer {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

.profile {
  border-radius: 1rem;
  --tw-bg-opacity: 1;
  background-color: rgba(59, 130, 246, var(--tw-bg-opacity));
  background-color: hsl(235, 46%, 20%);
}

@media (min-width: 1024px) {
  .profile {
    grid-row: span 2 / span 2;
  }
}

.profile-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-radius: 1rem;
  padding: 2rem;
  background-color: hsl(246, 80%, 60%);
}

@media (min-width: 1024px) {
  .profile-header {
    flex-direction: column;
    align-items: flex-start;
  }
}

.profile-header-photo {
  width: 20%;
}

@media (min-width: 1024px) {
  .profile-header-photo {
    width: 40%;
  }
}

.profile-header-photo-img {
  max-width: 100%;
  border-radius: 9999px;
  border-width: 2px;
  border-color: white;
}

.profile-header-text {
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}

@media (min-width: 1024px) {
  .profile-header-text {
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
  }
}

.profile-header-text-report {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: hsl(236, 100%, 87%);
}

.profile-header-text-name {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 400;
  color: hsl(236, 100%, 87%);
}

@media (min-width: 1024px) {
  .profile-header-text-name {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
}

.profile-footer {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}

@media (min-width: 1024px) {
  .profile-footer {
    flex-direction: column;
  }
}

.profile-footer-btn {
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  color: hsl(235, 45%, 61%);
}

.profile-footer-btn:hover {
  color: hsl(236, 100%, 87%);
}

.modal-stat {
  border-radius: 1rem;
}

.modal-stat:hover {
  cursor: pointer;
  opacity: 0.8;
}

.modal-stat-header {
  position: relative;
  z-index: 10;
  height: 2.5rem;
  overflow-y: hidden;
}

.modal-stat-header-img {
  position: absolute;
  top: -0.5rem;
  right: 0.5rem;
  z-index: 20;
  display: block;
}

.modal-stat-footer {
  position: relative;
  z-index: 30;
  display: block;
  border-radius: 1rem;
  padding: 1.25rem;
  background-color: hsl(235, 46%, 20%);
}

.modal-stat-footer-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal-stat-footer-top-activity {
  font-weight: 700;
  color: hsl(236, 100%, 87%);
}

.modal-stat-footer-top-options {
  cursor: pointer;
  padding: 0.25rem;
}

.modal-stat-footer-bottom {
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 1024px) {
  .modal-stat-footer-bottom {
    flex-direction: column;
    align-items: flex-start;
  }
}

.modal-stat-footer-bottom-time {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 300;
  color: hsl(236, 100%, 87%);
}

@media (min-width: 1024px) {
  .modal-stat-footer-bottom-time {
    margin-top: 1rem;
    font-size: 3rem;
    line-height: 1;
  }
}

.modal-stat-footer-bottom-last {
  color: hsl(235, 45%, 61%);
}

